.ls-slider-wrapper{height:500px}ls-slider .slider-inside{position:relative;height:100%;background:gray;overflow:hidden;white-space:nowrap;font-size:0}ls-slider .hold-images{position:absolute;top:0;left:0;bottom:0;right:0;transition:1s all}ls-slider .overlay{position:absolute;top:0;left:0;bottom:0;right:0;z-index:2;background:rgba(0,0,0,0.2)}ls-slider .img-container{width:100%;height:100%;display:inline-block;background-size:cover}ls-slider .transcluded-content{position:absolute;font-size:1rem;z-index:3;top:0;left:0;right:0;bottom:0}ls-slider .custom-lead{flex-grow:1}ls-slider .lead-border{font-weight:300;margin-bottom:20px;letter-spacing:1px;font-family:"Montserrat", arial, sans-serif;text-align:center;border:2px solid;outline:#FFF thick;color:#fff;border-radius:25px;margin-top:-41px;padding-top:41px;height:100%}ls-slider .lead-wrap-border{height:100%;overflow:hidden}ls-slider .slider-selector{color:#fff;font-size:3rem}ls-slider .container-width{width:85%}ls-slider .flex-column{display:flex;flex-direction:column}ls-slider .flex-row{display:flex;flex-direction:row}ls-slider .flex-grow{display:flex;flex:1}ls-slider .flex-grow-2{display:flex;flex:2}ls-slider .flex-grow-3{display:flex;flex:3}ls-slider .arrow-container{width:7.5%;color:#fff;font-size:18rem}ls-slider .horizontal-center{justify-content:center}ls-slider .vertical-center{align-items:center}ls-slider .align-left{justify-content:flex-start}ls-slider .align-right{justify-content:flex-end}ls-slider h2{font-size:5rem;font-weight:bold}
