.location-tree *,.location-tree *:focus{outline:0}.location-tree{width:200px;padding:8px 0;font-size:14px;border-radius:3px;box-shadow:0 0 5px 0 lightgrey;background-color:whitesmoke}.location-tree .collapse-btn{cursor:pointer}.location-tree .collapse-btn .icon{padding:0 5px}.location-tree .expand.ng-enter{transition:100ms ease-in-out all;opacity:0;max-height:0}.location-tree .expand.ng-enter-active{opacity:1;max-height:100px}.location-tree .expand.ng-leave{transition:100ms ease-in-out all;height:100px;max-height:100px;opacity:1}.location-tree .expand.ng-leave-active{height:0;max-height:0;opacity:0}.location-tree ul{padding-left:25px;margin:0}.location-tree li{padding:5px 8px;cursor:pointer}.location-tree li.active{color:#fff;background-color:#67346d;border-radius:2px}.location-tree li .icon{padding:0 5px}.location-tree .collapse-btn{cursor:pointer}.location-tree .empty-node{font-style:italic;color:grey;padding-left:36px}
